.page {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
}

.page__section {
    width: 100vw;
    height: 100vh;
    scroll-snap-align: start;
    padding-top: 10vh;
}

.backgroundDiv {
    position: fixed;
    opacity: var(--background-opacity);
    z-index: -1;
}

.backgroundImg {
  position: fixed;
  object-fit: cover;
  visibility: hidden;
  width: 100vw;
  height: 100vh;
}

@media screen and (max-width: 480px) {
    .page__section {
        padding-top: 3vh;
        height: 100vh;
        margin-top: 10vh;
    }
    
    #who {
        margin-top: 0;
        padding-top: 0;
    }
}