* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
:root {
    --text-color: white;
    --background-color: #383838;
    --hr-color: white;
  }
  
body {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: var(--text-color);
  }

.title {  
    padding-top: 3vh;
    font-size: 50px;
    width: 33vw;
    display: block;
    text-align: right;
    font-weight: bold;
}

.theme {
    position: fixed;
    height: 50px;
    width: 50px;
    border-radius: 100%;
    border: 0;
    font-size: 25px;
    margin-top: 80vh;
    margin-left: 92vw;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid;
  border-top-color: var(--hr-color);
  width: 35vw;
  margin: 1em 0;
  padding: 0;
}

a {
  color: var(--text-color);
  text-decoration:underline;
}

a:hover {
  color: var(--navbar-background);
}

@media screen and (max-width: 480px){
  .title {
    font-size: 10vw;
    width: 55vw;
  }

  hr {
    width: 63vw;
  }

  .theme {
    margin-left: 80vw;
    margin-top: 82vh;
  }
}
  