.objective__content {
    width: 80vw;
    margin-left: 10vw;
    margin-top: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.objective__text {
    width: 60vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 20px;
    text-align: justify;
    text-justify:inter-word;
}

.objective__image {
    width: 20vw;
    height: auto;
    border-radius: 20%;
}

.objective__text__cell{
    width: 40vw;
    margin: auto;
}

@media screen and (max-width: 480px) {

    .objective__content{
        width: 70vw;
        flex-direction: column;
        margin: auto;
        margin-top: -5vh;
    }

    .objective__text__cell {
        margin-top: 5vh;
        width: 70vw;     
        text-align: justify;
        text-justify: inter-word;
        word-break: break-all;
    }

    .objective__image {
        margin-top: 5vh;
        width: 70vw;
        height: auto;
    }

}