.conclusion__content {
    width: 90vw;
    margin-top: -15vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.conclusion__text {
    width: 60vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 20px;
}

.conclusion__image {
    width: 40vw;
    height: auto;
    margin: auto;
}

.conclusion__text__cell{
    width: 40vw;
    margin: auto;
}

.conclusion__important {
    margin-top: 5vh;
    text-align: center;
    font-size: 2rem;
}



@media screen and (max-width: 480px) {

    .conclusion__content{
        margin-left: 10vw;
        width: 70vw;
        flex-direction: column;
        margin: auto;
        margin-top: -5vh;
    }

    .conclusion__text__cell {
        margin-top: 5vh;
        width: 65vw;
        text-align: justify;
        text-justify: inter-word;
        word-break: break-all;
    }

    .conclusion__image {
        margin-top: 5vh;
        width: 70vw;
        height: auto;
    }

}