.mailSender {
    color: var(--text-color);
}

.mailSender:hover {
    color: blue;
}

.contacts__pages {
    margin-top: 5vw;
    margin-left: 10vw;
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content:space-between;
}

form {
    margin-top: 3vh;
}

.contacts__direct {
    margin-top: 2vh;
    display: flex;
    justify-content: space-between;
}

.contacts__nome {
    display: flex;
    justify-content: space-between;
}

.contacts__email {
    margin-top: 2vh;
    width: 18.5vw;
    display: flex;
    justify-content: space-between;
}

.contacts__messaggio {
    margin-top: 2vh;
    display: flex;
    justify-content: space-between;
}

.contacts__invia__btn {
    margin-top: 2vh;
    width: 18.5vw;
    height: 5vh;
    font-family: Arial, Helvetica, sans-serif;
}

.contacts__invia__btn:hover{
    color: white;
    background-color: var(--navbar-background-color);
    border-color: white;
}

.contacts__contact {
    margin-top: 2vh;
    font-weight: bold;
    font-size: 2.5vh;
}

.contacts__static {
    margin-left: 10vw;
}

.contacts__image{
    position: relative;
    margin-right: 20vw;
    width: 20vw;
    height: auto;
}

@media screen and (max-width: 480px) {

    .contacts__pages{
        flex-direction: column;
        margin-top: 5vh;
        width: 70vw;
    }

    .contacts__email {
        width: 70vw;
    }

    .contacts__invia__btn{
        width: 70vw;
    }

    .contacts__static{
        margin-left: 0;
        margin-top: 5vh;
        width: 70vw;
    }

    .contacts__image {
        visibility: hidden;
    }
}