.project__content {
    width: 90vw;
    margin-left: 3vw;
    margin-top: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.project__text {
    width: 70vw;
    display: flex;
    flex-direction: column;
    font-weight: bold;
    font-size: 20px;
}

.project__image {
    margin-top: -20vh;
    width: 45vw;
    height: auto; 
}

.project__text__cell{
    width: 40vw;
    margin: auto;
    font-weight: normal;
}

@media screen and (max-width: 480px) {

    .project__content{
        margin-left: 10vw;
        width: 70vw;
        flex-direction: column;
        margin: auto;
    }

    .project__text__cell {
        margin-top: 5vh;
        width: 70vw;
        text-align: justify;
        text-justify: inter-word;
        word-break: break-all;
    }

    .project__important {
        width: 100vw;
        margin-left: -15vw;
        text-align: center;
    }

    .project__image {
        margin-top: 5vh;
        width: 100vw;
        height: auto;
        margin-left: -15vw;
    }

}