.team__content {
    width: 80vw;
    margin-left: 5vw;
    margin-top: 10vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.team__text {
    width: 60vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-weight: bold;
    font-size: 20px;
}

.team__image {
    width: 20vw;
    height: auto;
    border-radius: 20%;
}

.team__text__cell{
    width: 40vw;
    margin: auto;
}

.team__name {
    font-size: 50px;
}

.team__slogan {
    font-size: 20px;
}

@media screen and (max-width: 480px) {

    .team__content{
        margin-left: 10vw;
        width: 70vw;
        flex-direction: column;
        margin: auto;
        margin-top: -5vh;
    }

    .team__text__cell {
        margin-top: 5vh;
        width: 70vw;
        text-align: center;
    }

    .team__image {
        margin-top: 5vh;
        width: 70vw;
        height: auto;
    }

}