.model__content {
    width: 95vw;
    margin-top: 10vh;
    display: flex;
    justify-content: space-between;
    grid-template-columns: 1fr 1fr 1fr;
    margin-left: 2.5vw;
}


.model__image {
    width: 30vw;
    height: auto;
    border-radius: 20%;
    border: 3px solid white;
}

.model__image__app {
    width: 30vw;
}


@media screen and (max-width: 480px) {

    .model__content{
        width: 100vw;
        flex-direction: column;
        margin-top: 5vh;
    }

    .model__image {
        margin-left: 20vw;
        margin-top: 2vh;
        width: 60vw;
    }

}