.navbar {
    position: fixed;
    height: 10vh;
    width: 100vw;
    display: flex;
    justify-content: space-between;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 100%;
    padding-left: 10vh;
    padding-right: 10vh;
    color: var(--navbar-text-color);
    font-weight: bold;

    background: var(--navbar-background);
    border-bottom: var(--navbar-border-bottom);
    box-shadow: var(--navbar-box-shadow);
}

.navbar__item {
    height: 100%;
    padding-top: 4vh;
}

.navbar__item:hover {
    color: var(--hover-color);
}

.navbar__information {
    margin-left: 30vw;
    margin-top: 13vh;
    height: 10vh;
    position: fixed;
    width: 37vw;
    justify-content: center;
    background-color: white;
    color: black;
    padding: 2.5vh;
    border: 2px solid red;
    border-radius: 20px;
    padding-top: 3vh;
}

#closeIcon {
    position: fixed;
    width: 25px;
    margin-left: 32vw;
    margin-top: -3vh;
}

.burger {
    visibility: hidden;
    position: fixed;
    margin-left: 10000vw;
}

@media screen and (max-width: 480px) {
    .navbar {
        height: 100vh;
        width: 100vw;
        color: black;
        background: 0;
        background-color: rgba(255, 255, 255, 0.7);
        display: flex;
        flex-direction: column;
        padding: 0;
        padding-top: 40%;
        padding-bottom: 80%;
        padding-left: 40%;
        visibility: hidden;
    }

    .navbar__information {
        visibility: hidden;
    }

    .burger {
        visibility: visible;
        position: fixed;
        margin-left: 85vw;
        margin-top: 5vh;
    }

    .burgerIcon {
        position: fixed;
        height: auto;
        width: auto;
        max-height: 30px;
        max-width: 30px;
    }
}
